/* individual Shop page */

.single-product .site-content{
  margin-left: auto;
  margin-right: auto;
  width: 1120px;
  // .content-area {
  //   padding-top: 3rem;
  //   padding-bottom: 3rem;
  //   margin-bottom: 4.5rem;
  // }
}
h1.single-product-title {   //***
  margin-top: 0;
  text-transform: uppercase;
  line-height: 1rem;
  letter-spacing: 1px;
  font-family: $font_secondary_wide;
  font-weight: 600;
  font-style: normal;
  font-size: 2.25rem;  
}
.single-product .site-content{
  display: flex;
  justify-content: space-between;
  .one-product {
    display: flex;
    justify-content: space-between;
  }
}

.product-img {
  width: 48%;
  margin-top: 3rem;  //***
  margin-bottom: 4rem; //***
}

.product-desc{
  width: 48%;
  margin-top: 3rem; //***
  margin-bottom: 4rem; //***

  .social-wrap {
    margin: 1.5rem 0;

    p {
      border: 1px solid #000;
      color: black;
    }

    a {
      text-decoration: none;
      color: black;
      white-space: 1rem;
    }
  }
}
.white-link {
  border: 0;
  background-color: transparent;
  text-transform: uppercase;
  font-family: $font_secondary_norm;
  font-weight: normal;
  font-style: normal;
  border: 1px solid black;
  color: black;
  font-size: 1rem;
  padding: .75rem 1.5rem;  

  .fa {      //***
    color: black;
  }

  &:hover {
    background: $color_green;

    a{
      color: $color_white;
    }

    .fa {
      color: $color_white;
    }
  }  

}

div p {
  line-height: 1.75;
}

.single-product h1 {
  color: $color_orange;
}
.single-product .price {
  font-size: 1.8rem;
  margin: 0;
  text-transform: uppercase;
  font-family: $font_secondary_norm;
  font-weight: normal;
	font-style: normal;
}
