/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/

/* Buttons */

button,
input[type="button"],
input[type="reset"] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: $color_green;
	@include font-size(0.75);
	line-height: 1;
	padding: .6rem 1rem .4rem;
	

	&:hover {
		border-color: #ccc #bbb #aaa;
		box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.8), inset 0 15px 17px rgba(255, 255, 255, 0.8), inset 0 -5px 12px rgba(0, 0, 0, 0.02);
	}

	&:active,
	&:focus {
		border-color: #aaa #bbb #bbb;
		box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.15);
	}
}

input[type="submit"] {
	border: 1px solid;
	background: $color_green;
	color: $color_white;
	@include font-size(0.75);
	line-height: 1;
	padding: 0.65rem 1.75rem 0.65rem;
	text-transform: uppercase;
	font-family: $font_secondary_norm;
	font-weight: normal;
	font-style: normal;
	font-size: 1rem;

	&:hover {
		background-color: #19625d;
	}

	&:active,
	&:focus {
		border-color: #aaa #bbb #bbb;
		box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.15);
	}
}


/* Fields */

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	background: #f4f4f4;

	&:focus {
		color: #111;
	}
}

form p label {
	text-transform: uppercase;
	font-family: $font_secondary_norm;
	font-weight: normal;
	font-style: normal;
	font-size: 1.05em;
	line-height: 1rem;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
	padding: 3px;
}

textarea {
	padding-left: 3px;
	width: 100%;
}
